import Module from './decoder/decoder-pro-simd'
import ModuleAudio from './decoder/decoder-pro-audio';
import workerPostRun from './worker-run';

const promiseList = [];

promiseList.push(ModuleAudio({
    printErr: function (text) {
        console.warn('JbPro[\u274C\u274C\u274C][worker]:', text);
    }
}), Module({
    printErr: function (text) {
        console.warn('JbPro[\u274C\u274C\u274C][worker]:', text);
    }
}));

Promise.all(promiseList).then((list) => {
    const mAudio = list[0];
    const mVideo = list[1];
    workerPostRun(mVideo, mAudio);
})


